import React from 'react';
import { storiesOf } from '@storybook/react';

import { withKnobs, select, text } from '@storybook/addon-knobs';
import TooltipDefinition from '../TooltipDefinition';

const directions = {
  'Bottom (bottom)': 'bottom',
  'Top (top)': 'top',
};

const alignments = {
  'Start (start)': 'start',
  'Center (center)': 'center',
  'End (end)': 'end',
};

const props = () => ({
  triggerClassName: text(
    'Trigger element CSS class name (triggerClassName)',
    ''
  ),
  direction: select('Tooltip direction (direction)', directions, 'bottom'),
  align: select(
    'Tooltip alignment to trigger button (align)',
    alignments,
    'start'
  ),
  tooltipText: text(
    'Tooltip content (tooltipText)',
    'Brief description of the dotted, underlined word above.'
  ),
});

storiesOf('TooltipDefinition', module).addParameters({ storySource: { source: __STORY__, locationsMap: __ADDS_MAP__ } }).addDecorator(withSourceLoader(__STORY__, __ADDS_MAP__,__MAIN_FILE_LOCATION__,__MODULE_DEPENDENCIES__,__LOCAL_DEPENDENCIES__,__SOURCE_PREFIX__,__IDS_TO_FRAMEWORKS__))
  .addDecorator(withKnobs)
  .add(
    'default',
    () => (
      <div style={{ marginTop: '2rem' }}>
        <TooltipDefinition {...props()}>Definition Tooltip</TooltipDefinition>
      </div>
    ),
    {
      info: {
        text: `
          Definition tooltip is for regular use case of tooltip, e.g. giving the user more text information about something, like defining a word.
          This works better than the interactive tooltip in regular use cases because the info icon used in interactive tooltip can be repetitive when its shown several times on a page.
          Definition tooltip does not use any JavaScript. If there are actions a user can take in the tooltip (e.g. a link or a button), use interactive tooltip.
        `,
      },
    }
  );

  