import React from 'react';
import { storiesOf } from '@storybook/react';
import { Filter16 } from '@carbon/icons-react';
import { withKnobs, select, text } from '@storybook/addon-knobs';
import TooltipIcon from '../TooltipIcon';

const directions = {
  'Top (top)': 'top',
  'Right (right)': 'right',
  'Bottom (bottom)': 'bottom',
  'Left (left)': 'left',
};

const alignments = {
  'Start (start)': 'start',
  'Center (center)': 'center',
  'End (end)': 'end',
};

const props = () => ({
  direction: select('Tooltip direction (direction)', directions, 'bottom'),
  align: select('Tooltip alignment (align)', alignments, 'center'),
  tooltipText: text('Tooltip content (tooltipText)', 'Filter'),
});

storiesOf('TooltipIcon', module).addParameters({ storySource: { source: __STORY__, locationsMap: __ADDS_MAP__ } }).addDecorator(withSourceLoader(__STORY__, __ADDS_MAP__,__MAIN_FILE_LOCATION__,__MODULE_DEPENDENCIES__,__LOCAL_DEPENDENCIES__,__SOURCE_PREFIX__,__IDS_TO_FRAMEWORKS__))
  .addDecorator(withKnobs)
  .add(
    'default',
    () => (
      <TooltipIcon {...props()}>
        <Filter16 />
      </TooltipIcon>
    ),
    {
      info: {
        text: `
          Icon tooltip is for short single line of text describing an icon.
          Icon tooltip does not use any JavaScript. No label should be added to this variation.
          If there are actions a user can take in the tooltip (e.g. a link or a button), use interactive tooltip.
        `,
      },
    }
  );

  