import React, { useState } from 'react';
import { storiesOf } from '@storybook/react';
import { action } from '@storybook/addon-actions';
import { ErrorBoundary, ErrorBoundaryContext } from './';
import Button from '../Button';

storiesOf('ErrorBoundary', module).addParameters({ storySource: { source: __STORY__, locationsMap: __ADDS_MAP__ } }).addDecorator(withSourceLoader(__STORY__, __ADDS_MAP__,__MAIN_FILE_LOCATION__,__MODULE_DEPENDENCIES__,__LOCAL_DEPENDENCIES__,__SOURCE_PREFIX__,__IDS_TO_FRAMEWORKS__))
  .add('default', () => {
    function DemoComponent() {
      const [shouldThrowError, setShouldThrowError] = useState(false);

      function onClick() {
        setShouldThrowError(!shouldThrowError);
      }

      return (
        <>
          <Button onClick={onClick}>Toggle throwing error</Button>
          <div>
            <ErrorBoundary fallback={<Fallback />}>
              <ThrowError shouldThrowError={shouldThrowError} />
            </ErrorBoundary>
          </div>
        </>
      );
    }

    function Fallback() {
      return 'Whoops';
    }

    function ThrowError({ shouldThrowError }) {
      if (shouldThrowError) {
        throw new Error('Component threw error');
      }

      return 'Successfully rendered';
    }

    return <DemoComponent />;
  })
  .add('with custom context', () => {
    function DemoComponent() {
      const [shouldThrowError, setShouldThrowError] = useState(false);

      function onClick() {
        setShouldThrowError(!shouldThrowError);
      }

      return (
        <ErrorBoundaryContext.Provider value={{ log: action('log') }}>
          <Button onClick={onClick}>Toggle throwing error</Button>
          <div>
            <ErrorBoundary fallback={<Fallback />}>
              <ThrowError shouldThrowError={shouldThrowError} />
            </ErrorBoundary>
          </div>
        </ErrorBoundaryContext.Provider>
      );
    }

    function Fallback() {
      return 'Whoops';
    }

    function ThrowError({ shouldThrowError }) {
      if (shouldThrowError) {
        throw new Error('Component threw error');
      }

      return 'Successfully rendered';
    }

    return <DemoComponent />;
  });

  